	module.exports = {"pathContext":{"group":[{"node":{"id":"f5c20c54-1a63-5a89-b060-7c2c0ec72237","slug":"around-1-in-10-young-mentally-ill-teens-drinks-smokes-and-uses-cannabis","title":"Around 1 in 10 young mentally ill teens drinks, smokes, and uses cannabis","excerpt":"<p>Around one in 10 young teens with mental health issues also drinks alcohol, smokes cigarettes, and uses cannabis on a weekly basis, indicates Australian research published in BMJ Open. The prevalence of this pattern of substance use increased with age, the study found, prompting the authors to raise concerns that these behaviours are likely to [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2013/02/05/around-1-in-10-young-mentally-ill-teens-drinks-smokes-and-uses-cannabis/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 05, 2013","template":"","format":"standard"}},{"node":{"id":"8043666f-0fcf-54b3-9e78-8eb8fe041d9e","slug":"using-the-spirit-statement-to-improve-trial-protocols","title":"Using the SPIRIT statement to improve trial protocols","excerpt":"<p>We have updated our instructions for authors to show that we now encourage the use of the SPIRIT statement. SPIRIT (Standard Protocol Items: Recommendations for Interventional Trials) is ‘an international initiative that aims to improve the quality of clinical trial protocols by defining an evidence-based set of items to address in a protocol’. Its creation [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2013/01/18/using-the-spirit-statement-to-improve-trial-protocols/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 18, 2013","template":"","format":"standard"}},{"node":{"id":"3b5394aa-df5e-57e9-accf-bfcbe1e34de3","slug":"2012-year-in-review","title":"2012 year in review","excerpt":"<p>&nbsp; 2012 was a hugely successful year for BMJ Open. We published four times as many papers as in 2011. Credit for this must go, first and foremost, to the hundreds of reviewers who have given their time to assess manuscripts. We are also grateful that so many authors have chosen to publish with us. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2013/01/11/2012-year-in-review/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 11, 2013","template":"","format":"standard"}},{"node":{"id":"0fbd1641-1340-56f8-8234-6eda3dc770b5","slug":"most-read-articles-in-december-2","title":"Most read articles in December","excerpt":"<p>The most read article in December was Bellis and colleagues&#8217; study of mortality amongst rock and pop stars, which also received a great deal of press attention. The second most read was Rajavashisth et al&#8216;s study of diabetes prevalence amongst marijuana users which was originally published way back in February 2012. Bergman et al&#8216;s newly published paper on [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2013/01/09/most-read-articles-in-december-2/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 09, 2013","template":"","format":"standard"}},{"node":{"id":"78a7cdcc-cee7-590a-8781-a51800758f03","slug":"most-read-articles-in-november-2","title":"Most read articles in November","excerpt":"<p>The most read article in November was Arroll and Howard&#8217;s newly published prospective study on therapeutic approaches to myalgic encephalomyelitis/chronic fatigue syndrome. The second most read was also a new study, by Liao and colleagues, on the content and quality of information in online advertisements for female genital cosmetic surgery. Rank Author(s) Title 1 Arroll [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/12/11/most-read-articles-in-november-2/\">Read More&#8230;</a></p>\n","status":"publish","date":"December 11, 2012","template":"","format":"standard"}},{"node":{"id":"579366fd-26b6-5933-8654-d1f13650af18","slug":"most-read-articles-in-october-2","title":"Most read articles in October","excerpt":"<p>The most read article in October was Laursen and colleagues&#8217; newly published study of the effects of exercise volume and intensity upon the risk of metabolic syndrome. For the second month running, Ekström et al&#8216;s cohort study of the effectiveness of metformin was second most read and Kripke et al&#8216;s study of hypnotics and mortality remains the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/11/13/most-read-articles-in-october-2/\">Read More&#8230;</a></p>\n","status":"publish","date":"November 13, 2012","template":"","format":"standard"}},{"node":{"id":"d1a9cd2b-21e9-555a-8b53-5cb051bff3d4","slug":"most-reads-articles-in-september","title":"Most reads articles in September","excerpt":"<p>The most read article in September was Aston and colleagues&#8217; modelling study of meat consumption and greenhouse gas emissions in the UK. Ekström et al&#8216;s cohort study of the effectiveness of metformin was the second most read and Taylor et al&#8216;s study of the impact of substance misuse remains highly read in third place. Click on [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/10/11/most-reads-articles-in-september/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 11, 2012","template":"","format":"standard"}},{"node":{"id":"20ed0edf-eca2-5d19-bb3e-88ee201c2cfc","slug":"most-read-articles-in-august","title":"Most read articles in August","excerpt":"<p>The most read article in August was Taylor and colleagues&#8217; analysis of the relative risk of harm associated with substance misuse, which was originally published at the end of July. Click on the titles to read the articles in full. Rank Author(s) Title 1 Taylor et al Quantifying the RR of harm to self and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/09/18/most-read-articles-in-august/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 18, 2012","template":"","format":"standard"}},{"node":{"id":"dd157ef1-22e0-547d-8636-8a7ffa96dc96","slug":"most-read-articles-in-july","title":"Most read articles in July","excerpt":"<p>The most read article in July was Heneghan and colleagues&#8217; analysis of sports performance products, which received widespread press attention and was also accompanied by a BBC Panorama special. The second most read paper, Katzmarzyk and Lee&#8217;s life table analysis of life expectancy and sedentary behaviour, also received large amounts of press coverage both in [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/08/09/most-read-articles-in-july/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 09, 2012","template":"","format":"standard"}},{"node":{"id":"96c15648-8855-5545-b35b-316379412bff","slug":"bmj-open-in-the-news","title":"BMJ Open in the news","excerpt":"<p>&nbsp; BMJ Open research has made the news again. The paper by Peter T Katzmarzyk (Pennington Biomedical Research Center) and I-Min Lee (Harvard) on the impact of sitting and television viewing on life expectancy in the USA has received widespread coverage, from the BBC in the UK, through numerous US news outlets, and as far as [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/07/11/bmj-open-in-the-news/\">Read More&#8230;</a></p>\n","status":"publish","date":"July 11, 2012","template":"","format":"standard"}}],"pathPrefix":"posts","first":false,"last":false,"index":22,"pageCount":26,"additionalContext":{}}}

